const rand=(max,min)=> Math.floor(Math.random()*(max-min)+min);
const geraMaiuscula = () => String.fromCharCode(rand(65,91)); //65 91 codigo letras maiusculas tabela ascii
const geraMinuscula = () => String.fromCharCode(rand(97,123)); // codigo letras minusculas tabela ascii
const geraNumero = () => String.fromCharCode(rand(48,58)); // codigo letras minusculas tabela ascii
const simbolos ='!@#$%Â¨&*()_-=+{}[]<>,./?:;';
const geraSimbolo = () => simbolos[rand(0,simbolos.length)];



export default function geraSenha(qtd,maiusculas,minusculas,numeros,simbolos){
    const senhaArray = [];
    qtd=Number(qtd);
    for(let i=0;i<=qtd;i++){
        maiusculas && senhaArray.push(geraMaiuscula());
        minusculas && senhaArray.push(geraMinuscula());
        numeros && senhaArray.push(geraNumero());
        simbolos && senhaArray.push(geraSimbolo());
    }
    console.log("jjjj");
    return senhaArray.join('').slice(0,qtd);
}
